.card {
  background:lemonchiffon;
  border-radius: 10px;
  height: 200px;
  margin: 1rem;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}
.card:hover{
  width: 25%;
  cursor: pointer;
}
.card > .img-container {
  height: 100%;
  overflow: hidden;
  border-radius: 10px;
  text-align: center;
}
.card > .img-container > img {
  width: 60%;

}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}


